import React from 'react';
import { withRouter } from 'react-router-dom'
import {TabBar} from 'antd-mobile'
import store from '../redux/store'

class Mytab extends React.Component {
	render() {
		const {pathname} = this.props.location;
	  	return (
		<div style={{ position: 'fixed', width: '100%', bottom: 0 }}>
		  <TabBar
			unselectedTintColor="#949494"
			tintColor="#33A3F4"
			barTintColor="white"
		  >	
		  	<TabBar.Item
			  icon={<i className='iconfont icon-Homehomepagemenu'></i>}
			  selectedIcon={<i style={{fontSize:22}} className='iconfont icon-Homehomepagemenu1'></i>}
			  title="首页"
			  key="home"
			  badge={'new'}
			  selected={pathname === '/home'}
			  onPress={() => {
                    this.props.history.push('/home')
			  }}
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={<i className='iconfont icon-shoucang'></i>}
			  selectedIcon={<i style={{fontSize:22}} className='iconfont icon-shoucang1'></i>}
			  title="收藏"
              key="shoucang"
			  dot
			  onPress={() => {
				if(!store.getState().home.username){
					this.props.history.push('/login')
				}else{
					this.props.history.push('/collect')
				}
              }}
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={<i className='iconfont icon-dingdan1'></i>}
			  selectedIcon={<i style={{fontSize:22}} className='iconfont icon-dingdan'></i>}
			  title="订单列表"
			  key="Dingdan"
			  selected={pathname === '/dingdan'}
			  onPress={() => {
				this.props.history.push('/dingdan')
              }}
			>
			</TabBar.Item>
			<TabBar.Item
			  title="我的淘宝"
			  key="home"
			  icon={<i  className='iconfont icon-wode'></i>}
			  selectedIcon={<i style={{fontSize:22}} className='iconfont icon-wodedangxuan'></i>}
			  selected={pathname === '/mytaobao'}
			  badge={1}
			  onPress={() => {
                this.props.history.push('/mytaobao')
			  }}
			>
			</TabBar.Item>
		  </TabBar>
		</div>
	  );
	}
  }
export default withRouter(Mytab);