import React from 'react';
import { HashRouter as Router, Redirect, Route, Switch } from 'react-router-dom'
import Home from './taobao/Home';
import Collect from './taobao/Collect';
import Dingdan from './taobao/Dingdan';
import Mytaobao from './taobao/Mytaobao';
import Search from './taobao/Search';
import Info from './taobao/Info';
import Login from './taobao/Login'

import store from './redux/store';
import { Provider } from 'react-redux'

const App = ()=>{
	return (
		<Provider store={store}>
			<Router>
				<Switch>
					<Redirect exact from="/" to="/home" />
					<Route path='/home' component={Home} />
					<Route path='/collect' component={Collect} />
					<Route path='/dingdan' component={Dingdan} />
					<Route path='/mytaobao' component={Mytaobao} />	
					<Route path='/search' component={Search} />
					<Route path='/info/:id' component={Info} />
					<Route path='/login' component={Login} />
				</Switch>		
			</Router> 
		</Provider>		
	)
}

export default App;