import React from 'react'
import {TabBar,Button} from 'antd-mobile'

import { connect } from 'react-redux'
import store from '../redux/store'

const Info = (props) => {
    console.log(props)

    const data = store.getState().data.datalist
    console.log(data)
    const imgid = props.match.params.id
    var imgsrc,title,price;
    for(var i=0; i<data.length; i++){
        if(data[i].id == imgid)
        {
            imgsrc = data[i].img
            title = data[i].title
            price = data[i].price
        }        
    }
 
    return (
        <div>
            <img 
            style={{width:'100%'}} 
            src={imgsrc} 
            alt=""      
            />
            <div style={{position: 'fixed', top: 0,width:'80px',height:'80px'}}>
                <TabBar tintColor="#33A3F4"
                    barTintColor="rgba(255,255,255,0)">
                <TabBar.Item 
                    icon={<i style={{fontSize:35}} className='iconfont icon-fanhui'></i>}
                    key="fanhui"
                    onPress={() => {
                            props.history.push('/home')
                    }}>
                </TabBar.Item>
                </TabBar>
            </div>
            <div>
                <p>{title}</p>
                <font color="#ff5500">￥ {price}</font>
            </div>
            <div style={{ position: 'fixed', width: '100%',bottom: 0 ,backgroundcolor:'white'}}>
                <div style={{ position: 'fixed', width: '35%',bottom: 0 }}>
                    <i style={{fontSize:30, marginRight:15}} className='iconfont icon-dianpu'></i>
                    <i style={{fontSize:30, marginRight:15}} className='iconfont icon-kefu'></i>
                    <i style={{fontSize:30, marginRight:15}} 
                    onClick={() =>{
                        if(!store.getState().home.username){
                            props.history.push('/login')
                        }else{
                        props.dispatch({
                            type:'ADD',
                            list:{
                                id:imgid,
                                img:imgsrc,
                                title:title,
                                price:price
                            }
                        })
                        let i = document.getElementsByTagName("i");
                        i[3].className='iconfont icon-shoucang1'
                        }
                    }} 
                    className='iconfont icon-shoucang'></i>
                </div> 
                <div style={{position: 'fixed', width: '65%', bottom: 0 ,right: 0 }}>
                    <Button 
                        style={{float:'right',width:'50%',
                        backgroundColor:'#ff7401',color:'white',
                        fontSize:'16px',fontWeight:'bold',
                        borderRadius:'0 50PX 50PX 0'}}>
                            立即购买</Button>
                    <Button 
                        style={{backgroundColor:'#ffbf00',color:'white',
                        fontSize:'16px',fontWeight:'bold',
                        borderRadius:'50px 0 0 50px'}}>
                            加入购物车</Button>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
    shoucang: state.collect
})
export default connect(mapStateToProps)(Info);

