
let shoucang = [];
const collectreducer = (state=shoucang,action)=>{
    if(action.type=='ADD'){
        console.log('reducer',action);
        return [...state,action.list]
    }
    if(action.type=='DEL'){
        // action.index
        let arr = [...state];
        arr.splice(action.index,1);
        return arr;
    }
    return state;
}

export default collectreducer;