import React from 'react'
import Mytab from './Mytab'

import {connect} from 'react-redux'
import store from '../redux/store'

const Collect = (props) => {
    const data = store.getState().collect
    console.log(data)

    return (
        <div>
             {
                props.shoucang.map((item,idx)=>(
                    <li key={idx}>
                        <img src={item.img} style={{height:'100px',weight:'100px'}} />
                        <p>{item.id} {item.title}</p>
                        <font color="#ff5500">￥ {item.price}</font>
                        <button
                            onClick={()=>{
                                props.dispatch({
                                    type: "DEL",
                                    index: idx
                                })
                            }}
                        >删除</button>
                    </li>
                ))
            }
            <Mytab />
        </div>
    )
}

const mapStateToProps = (state)=>{
    return {
        shoucang:state.collect
    }
}
export default connect(mapStateToProps)(Collect)