import React, { Component } from 'react'
import { SearchBar,Carousel} from 'antd-mobile';
import Mytab from './Mytab'

import { connect } from 'react-redux'
import {getdata} from '../redux/actionCreators'
import store from '../redux/store'

// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>

class Home extends Component {
    Change =(id)=>{
        this.props.history.push('/info/'+id);
    }

    baobeiList(){
        if(this.props.datalist.length ==0)
            return;
        else{
            console.log(this.props.datalist);
            const data = this.props.datalist;
            //将得到的datalist值传入store中
            let action = { type : 'GETDATA' , datalist: this.props.datalist }   
            store.dispatch( action ) 
            return(
                <div> 
                <div style={{width:'50%',height:'250px',float:'left',marginTop:'20px'}}  onClick={() =>this.Change(data[0].id)}>
                    <div style={{width:'90%',height:'250px',backgroundColor:'white',margin: '0 auto'}}>
                        <img 
                        style={{width:'100%',height:'70%'}} 
                        src={data[0].img} 
                        alt=""      
                        />
                        <p>{data[0].title}</p>
                        <font color="#ff5500">￥ {data[0].price}</font>
                    </div>    
                </div>
                <div style={{width:'50%',height:'250px',float:'right',marginTop:'20px'}}  onClick={() =>this.Change(data[1].id)}>
                    <div style={{width:'90%',height:'250px',backgroundColor:'white',margin: '0 auto'}}>
                        <img 
                        style={{width:'100%',height:'70%'}} 
                        src={data[1].img} 
                        alt=""                
                        />
                        <p>{data[1].title}</p>
                        <font color="#ff5500">￥ {data[1].price}</font>
                    </div>    
                </div>

                <div style={{width:'50%',height:'250px',float:'left',marginTop:'20px'}}  onClick={() =>this.Change(data[2].id)}>
                    <div style={{width:'90%',height:'250px',backgroundColor:'white',margin: '0 auto'}}>
                        <img 
                        style={{width:'100%',height:'70%'}} 
                        src={data[2].img} 
                        alt=""      
                        />
                        <p>{data[2].title}</p>
                        <font color="#ff5500">￥ {data[2].price}</font>
                    </div>    
                </div>
                <div style={{width:'50%',height:'250px',float:'right',marginTop:'20px'}}  onClick={() =>this.Change(data[3].id)}>
                    <div style={{width:'90%',height:'250px',backgroundColor:'white',margin: '0 auto'}}>
                        <img 
                        style={{width:'100%',height:'70%'}} 
                        src={data[3].img} 
                        alt=""                
                        />
                        <p>{data[3].title}</p>
                        <font color="#ff5500">￥ {data[3].price}</font>
                    </div>    
                </div>

                <div style={{width:'50%',height:'250px',float:'left',marginTop:'20px'}}  onClick={() =>this.Change(data[4].id)}>
                    <div style={{width:'90%',height:'250px',backgroundColor:'white',margin: '0 auto'}}>
                        <img 
                        style={{width:'100%',height:'70%'}} 
                        src={data[4].img} 
                        alt=""      
                        />
                        <p>{data[4].title}</p>
                        <font color="#ff5500">￥ {data[4].price}</font>
                    </div>    
                </div>
                <div style={{width:'50%',height:'250px',float:'right',marginTop:'20px'}}  onClick={() =>this.Change(data[5].id)}>
                    <div style={{width:'90%',height:'250px',backgroundColor:'white',margin: '0 auto'}}>
                        <img 
                        style={{width:'100%',height:'70%'}} 
                        src={data[5].img} 
                        alt=""                
                        />
                        <p>{data[5].title}</p>
                        <font color="#ff5500">￥ {data[5].price}</font>
                    </div>    
                </div>

                <div style={{width:'50%',height:'250px',float:'left',marginTop:'20px'}}  onClick={() =>this.Change(data[6].id)}>
                    <div style={{width:'90%',height:'250px',backgroundColor:'white',margin: '0 auto'}}>
                        <img 
                        style={{width:'100%',height:'70%'}} 
                        src={data[6].img} 
                        alt=""      
                        />
                        <p>{data[6].title}</p>
                        <font color="#ff5500">￥ {data[6].price}</font>
                    </div>    
                </div>
                <div style={{width:'50%',height:'250px',float:'right',marginTop:'20px'}}  onClick={() =>this.Change(data[7].id)}>
                    <div style={{width:'90%',height:'250px',backgroundColor:'white',margin: '0 auto'}}>
                        <img 
                        style={{width:'100%',height:'70%'}} 
                        src={data[7].img} 
                        alt=""                
                        />
                        <p>{data[7].title}</p>
                        <font color="#ff5500">￥ {data[7].price}</font>
                    </div>    
                </div>

                <div style={{width:'50%',height:'250px',float:'left',marginTop:'20px'}}  onClick={() =>this.Change(data[8].id)}>
                    <div style={{width:'90%',height:'250px',backgroundColor:'white',margin: '0 auto'}}>
                        <img 
                        style={{width:'100%',height:'70%'}} 
                        src={data[8].img} 
                        alt=""      
                        />
                        <p>{data[8].title}</p>
                        <font color="#ff5500">￥ {data[8].price}</font>
                    </div>    
                </div>
                <div style={{width:'50%',height:'250px',float:'right',marginTop:'20px'}}  onClick={() =>this.Change(data[9].id)}>
                    <div style={{width:'90%',height:'250px',backgroundColor:'white',margin: '0 auto'}}>
                        <img 
                        style={{width:'100%',height:'70%'}} 
                        src={data[9].img} 
                        alt=""                
                        />
                        <p>{data[9].title}</p>
                        <font color="#ff5500">￥ {data[9].price}</font>
                    </div>    
                </div>
                </div>
            )   
        }        
    }

    componentWillMount(){
        this.props.dispatch(getdata())
    }
    render() { 
        const data = []
        return (
            <div>
                <SearchBar placeholder="寻找宝贝店铺" 
                style={{background:'orange'}} 
                onFocus={()=>this.props.history.push('/search')}>
                </SearchBar>
                <Carousel autoplay infinite autoplayInterval={3000}>
                    <img 
                    style={{width:'100%'}} 
                    src={'https://gw.alicdn.com/imgextra/i3/111/O1CN01fhP0rv1CgsatV3Imq_!!111-0-lubanu.jpg'} 
                    alt=""
                    />
                    <img 
                    style={{width:'100%'}} 
                    src={'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg'} 
                    alt=""
                    />
                    <img 
                    style={{width:'100%'}} 
                    src={'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg'} 
                    alt=""
                    />
                    <img 
                    style={{width:'100%'}} 
                    src={'https://gw.alicdn.com/imgextra/i3/189/O1CN0101BLCD1DGbUJJRfeY_!!189-0-lubanu.jpg'} 
                    alt=""
                    />
                    <img 
                    style={{width:'100%'}} 
                    src={'https://gw.alicdn.com/imgextra/i3/29/O1CN01wYa8xO1C5K7Ka9quz_!!29-0-lubanu.jpg'} 
                    alt=""
                    /> 
                    <img 
                    style={{width:'100%'}} 
                    src={'https://gw.alicdn.com/imgextra/i1/21/O1CN01UypFuZ1C1ewqK0v9C_!!21-0-ppp-picassogw.jpg'} 
                    alt=""
                    /> 
                    <img 
                    style={{width:'100%'}} 
                    src={'https://gw.alicdn.com/imgextra/i2/160/O1CN01jZXP2p1D3K06qncRt_!!160-0-lubanu.jpg'} 
                    alt=""
                    />     
                </Carousel>
                {this.baobeiList(data)}
                <Mytab /> 	
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    datalist: state.data.datalist
})
export default connect(mapStateToProps)(Home);
    
